@mixin flex($direction: row, $main-axis: center, $cross-axis: center)
{
  display: flex;
  flex-direction: $direction;
  justify-content: $main-axis;
  align-items: $cross-axis;
}
@mixin grid($columns, $rows)
{
  display: grid;
  grid-template-columns: repeat($columns, 1fr);
  grid-template-rows: repeat($rows, 1fr);
}
@mixin setColor($color, $backgroundColor)
{
  color: $color;
  background-color: $backgroundColor;
}
@mixin generic-button($backgroundColor, $color)
{
  padding: 0.3rem 0.6rem;
  border-radius: 0.3rem;
  @include setColor($color, $backgroundColor);
  height: auto;
  max-height: 100%;
}

$black: #222;
$main: #4d5a97;

@mixin generic-text-button($backgroundColor: $black, $color: whitesmoke)
{
  @include generic-button($backgroundColor, $color);
  font-size: 1.2rem;
}
@mixin generic-icon-button($height: 100%, $backgroundColor: $black, $color: whitesmoke)
{
  @include generic-button($backgroundColor, $color);
  @include flex();
  i {font-size: 1.5rem;}
}
