@import 'mixins';

.app
{
  min-height: 100vh;
  @include flex();
  @include setColor(whitesmoke, $black);

  .container
  {
    width: 100%;
    @include flex(row, center, flex-start);
    gap: 0.6rem;
    .image
    {
      width: 60%;
      max-width: 800px;
      @include flex(column);
      gap: 0.6rem;
      padding: 0.6rem;
      border-radius: 0.3rem;
      background-color: $main;
      font-size: 1.2rem;
      .buttons
      {
        @include flex();
        gap: 0.6rem;
        > * { @include generic-text-button(); }
        label
        {
          cursor: pointer;
        }
        .download
        {
          font-size: inherit;
          text-decoration: none;
        }
      }
    }

    .controls
    {
      // width: 35%;
      background-color: $main;

      @include flex(column, center, flex-start);
      padding: 0.6rem;
      border-radius: 0.3rem;
      gap: 0.6rem;
      .colors
      {
        @include flex();
        gap: 0.6rem;
        .color
        {
          @include generic-text-button();
        }
        .color.selected
        {
          @include setColor($black, whitesmoke);
        }
      }
      .color-options
      {
        @include flex(column, center, flex-start);
        gap: 0.6rem;
        .options
        {
          @include flex();
          gap: 0.6rem;
          .color-option
          {
            @include generic-text-button();
          }
          .color-option.selected
          {
            @include setColor($black, whitesmoke);
          }
        }
        .custom-input-container
        {
          @include flex(column, center, flex-start);
          gap: 0.3rem;
          .custom-input
          {
            font-size: 1.2rem;
            @include flex();
            gap: 0.3rem;
            .title
            {
              width: 4rem;
            }
            input[type=number]
            {
              padding: 0.3rem;
              font-size: 1.2rem;
              width: 5rem;
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 768px)
  {
    flex-direction: column;
    .container
    {
      flex-direction: column;
      align-items: center;
      .image
      {
        width: 95% ;
      }

    }  
  }
}

.hidden
{
  display: none !important;
}

@media screen and (max-width: 1024px)
{
  html{font-size: 90%;}
}
@media screen and (max-width: 896px)
{
  html{font-size: 80%;}
}
@media screen and (max-width: 768px)
{
  html{font-size: 70%;}
}
@media screen and (max-width: 640px)
{
  html{font-size: 65%;}
}
@media screen and (max-width: 512px)
{
  html{font-size: 60%;}
}